import React, {useState} from "react";
import "../css/products.css";
import ethereumIcon from '../images/ethereumIcon.png';
import heliumIcon from '../images/heliumIcon.png';
import ProductBox from "./ProductBox";

const Products = () => {
  const [display, setDisplay] = useState(true);
  const [ethereum, setEthereum] = useState(false);
  const [helium, setHelium] = useState(false);

  const handleEthereum = () => {
    setDisplay(false);
    setEthereum(true);
  }
  const handleHelium = () =>{
    setDisplay(false);
    setHelium(true);
    
  }
  return (
    <div className="productDiv">
      <div className="svgDiv">
        <p>START YOUR MINING JOURNEY</p>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1440 320"
          className="svg"
        >
          <path
            fill="#fff"
            fill-opacity="1"
            d="M0,128L60,149.3C120,171,240,213,360,234.7C480,256,600,256,720,240C840,224,960,192,
          1080,170.7C1200,149,1320,139,1380,133.3L1440,128L1440,320L1380,320C1320,320,1200,320,
          1080,320C960,320,840,320,720,320C600,320,480,320,360,320C240,320,120,320,60,320L0,320Z"
          ></path>
        </svg>
      </div>
      <hr className="divider" />

     { display ? <div className="mainModal">
        <div className="childModal" onClick={handleEthereum}>
            <img src={ethereumIcon} alt="ethereumIcon"/>
            <p>Ethereum Mining</p>
        </div>

        <div className="childModal" onClick={handleHelium}>
          <img src={heliumIcon} alt="heliumIcon"/>
          <p>Helium Mining</p>
        </div>
      </div> : null}


      {ethereum ? <>
        <div className="rigsDiv">
          <ProductBox
            src="https://www.notebookcheck.net/fileadmin/Notebooks/News/_nc3/910022_amd_radeon_6600XT_angle1260x709_0.0.jpg"
            name="AMD 6600XT Rig : 150MH/s"
            gpu="5 GPU (brand - gigabyte/galax/inno3d)"
            power="300 watts"
            port="8 port mining motherboard"
            psu="PSU 750W modular"
            price="2,25,000"
            avgEarning="9k-12k"
          />
           <ProductBox
            src="https://static.nicehash.com/marketing%2FImage%20from%20iOS%20%281%29.jpg"
            name="Nvidia 3060 Rig: 175MH/s"
            gpu="5 GPU (brands : Zotac/gigabyte/galax/inno3D)"
            power="600 watts"
            port="8 port mining motherboard"
            psu="PSU 850W modular"
            price="2,50,000"
            avgEarning="10k-14k"
          />
      </div>

      <div className="rigsDiv">
          <ProductBox
            src="https://m.media-amazon.com/images/I/61Sc1YDgA0L._SL1500_.jpg"
            name="2060 super : 200 MH/s"
            gpu="5 GPU (brands : Zotac/gigabyte/galax/inno3D)"
            power="600 watts"
            port="8 port mining motherboard"
            psu="PSU 850W modular"
            price="2,95,000"
            avgEarning="11k-16k"
          />

          <ProductBox
            src="https://dlcdnwebimgs.asus.com/gain/5e1079c1-6d03-4d8e-bb40-dc9231a86e31/w800"
            name="3060 ti : 220MH/s"
            gpu="5 GPU (brands : Zotac/gigabyte/galax/inno3D)"
            power="600 watts"
            port="8 port mining motherboard"
            psu="PSU 850 watts modular"
            price="3,15,000"
            avgEarning="13k-17k"
          />
        </div>

        <div className="rigsDiv">
        <ProductBox
            src="https://cdn.vox-cdn.com/thumbor/VrdO-CsrWCGF4AXtENvZYM_7Mhg=/0x0:2640x1749/1200x675/filters:focal(1128x1043:1550x1465)/cdn.vox-cdn.com/uploads/chorus_image/image/69427255/rtx3070tilede_1.0.jpg"
            name="3070 ti : 300MH/s"
            gpu="5 GPU (brands : Zotac/gigabyte/galax/inno3D)"
            power="875 watts"
            port="8 port mining motherboard"
            psu="PSU 750W modular (2 units)"
            price="4,15,000"
            avgEarning="17k-22k"
          />
        </div>
      </> : null}

      {helium ? <div>THis is helium content</div> : null}
    </div>
  );
};

export default Products;
