import React from 'react';
import "../css/mining.css";

const Mining = () => {
  const cloud = ["Obtain a bitcoin wallet: Bitcoins are stored in digital wallets in an encrypted manner. This will keep your bitcoins safe.",
"Secure the wallet: Since there is no ownership on bitcoins, anyone who gains access to your wallet can use it without any restriction. So, enable two-factor authentication and store the wallet on a computer that does not have access to the Internet or store it in an external device.",
"Choose a cloud mining service provider: Cloud mining service providers allow users to rent processing or hashing power to mine bitcoins remotely. Popular cloud mining service providers are Genesis Mining and HashFlare.",
"Choose a cloud mining package: To choose a package, you will need to decide on how much you are willing to pay and keep your eyes open to the hashing power the package will offer. Cloud mining companies will mostly envisage the Return on Investment (ROI) based on the current market value of Bitcoins.",
"Pick a mining pool: This is the best shot you can get to earn bitcoins easily. There are many mining pools which charge a mere 2 percent of your total earnings. Over here, you will have to create workers which are basically subaccounts that can be used to track your contributions to the pool.",
"Put your earnings in your own secure wallet: Whenever you witness an ROI, simply withdraw your earnings and put them in your own secure wallet."];
  const own = ["Purchase custom mining hardware: You need to purchase an Application-specific Integrated Circuit (ASIC) miner to mine bitcoins. While purchasing an ASIC Blockchain miner, you should consider its efficacy in hashing power and take note of its pricing policies.",
"Purchase a power supply: Blockchain miners consume a lot of power. So, get a dependable power supply that is compatible with the ASIC miner that you purchase.",
"Obtain a bitcoin wallet: Bitcoins are stored in digital wallets in an encrypted manner. This will keep your bitcoins safe.",
"Secure the wallet: Since there is no ownership on bitcoins, anyone who gains access to your wallet can use it without any restriction. So, enable two-factor authentication and store the wallet on a computer that does not have access to the Internet or store it in an external device.",
"Pick a mining pool: This is the best shot you can get to earn bitcoins easily. There are many mining pools that charge a mere 2 percent of your total earnings. Over here, you will have to create workers which are basically subaccounts that can be used to track your contributions to the pool.",
"Connect the power supply to the ASIC Blockchain miner.", "Connect the ASIC Blockchain miner to your router.",
"Boot up your ASIC miner.", "Enter your router’s IP address in a web browser.", "Find ‘connected devices in the router miner page.",
"Find your ASIC miner and click on it to display the device information.","Copy and paste the IP address of your ASIC miner into your web browser.",
"Log in to the ASIC miner with the default username and password that are ‘Root’ and ‘Root.’", "Select ‘Miner Configuration’ to set up the miner according to your preferences.",
"Enter the URL, username, and password for your mining pool on the Miner Configuration page of the ASIC Miner.",
"Click ‘Save and Apply’ to save your credentials for future use.", "Start mining and in periodic intervals check your profitability.",
"Put your earnings in your own secure wallet: Whenever you witness an ROI, simply withdraw your earnings and put them in your own secure wallet."];

  return (
    <>
    <div className='miningDiv'>
      <h1 className='head'>What exactly Blockchain Mining is ?</h1>
      <p>
      Blockchain mining is a process to validate every step in the transactions while 
      operating bitcoins or other cryptocurrencies. The people involved here are known as 
      blockchain miners, and these miners’ function in a labyrinth of computational hardware 
      and software — their primary aim to authenticate the transfer of currency from a computer 
      in the network to another.<br/><br/>
      Blockchains are so-called because of their ‘blocks’ and ‘chain’ structure. The blocks are 
      composed of numerous bitcoins which are individual units that store all the data code 
      individually. The chain refers to the links from one neighbourhood block to another. Each 
      blockchain represents a specific code authentication explicitly encrypted on the network 
      software.<br/><br/>
      The process is rewarding, as well. A single user does not handle the mining process, but a 
      number of them compete on a unified authentication to get the rewards. Each mining success 
      comes with a bonus of several bitcoins.
      </p>
      </div>

      <div className='typesDiv'>
      <h1 className='head2'>Types of Mining</h1>
      <p>
      The process of mining can get really complex and a regular desktop or PC cannot cut it. 
      Hence, it requires a unique set of hardware and software that works well for the user. 
      It helps to have a custom set specific to mining certain blocks.
      The mining process undertaking can be divided into three categories:
    <br/><br/>
      <ol>
        <li><b>Individual Mining: </b>
        When mining is done by an individual, user registration as a miner is necessary. As soon 
        as a transaction takes place, a mathematical problem is given to all the single users in 
        the blockchain network to solve. The first one to solve it gets rewarded.<br/><br/>
        Once the solution is found, all the other miners in the blockchain network will validate 
        the decrypted value and then add it to the blockchain. Thus, verifying the transaction.
        </li><br/>
        <li><b>Pool Mining: </b>
        In pool mining, a group of users works together to approve the transaction. Sometimes, 
        the complexity of the data encrypted in the blocks makes it difficult for a user to decrypt 
        the encoded data alone. So, a group of miners works as a team to solve it. After the 
        validation of the result, the reward is then split between all users.
        </li><br/>
        <li><b>Cloud Mining: </b>
        Cloud mining eliminates the need for computer hardware and software. It’s a hassle-free 
        method to extract blocks. With cloud mining, handling all the machinery, order timings, 
        or selling profits is no longer a constant worry. <br/><br/>
        While it is hassle-free, it has its own set of disadvantages. The operational functionality 
        is limited with the limitations on bitcoin hashing. The operational expenses increase as the 
        reward profits are low. Software upgrades are restricted and so is the verification process.
        </li>
      </ol>
      </p>
      </div>

      <div className='mineDiv'>
        <h1 className='head'>How can you mine bitcoins?</h1>
        <p>
        You can buy and trade for bitcoins, or you can mine them. For mining bitcoins, users are 
        rewarded in bitcoins. This mechanism forms the pivot around which the bitcoin economy 
        revolves. While the cost and difficulty of mining bitcoins individually continue to increase,
        several cloud-based mining services have gradually emerged. These services allow individual 
        users to lease the processing power of mining equipment and mine bitcoins remotely. However, 
        you can mine bitcoins in person too.<br/><br/>

        <ul className='ul'>
          <li><h2>Mining Bitcoins on Cloud</h2>
        <ol>
          {cloud.map((list) => <li className='list'>{list}</li>)}
        </ol>
        </li>
        <li><h2>Mining Bitcoins on your own</h2>
        <ol>
          {own.map((list)=> <li className='list'>{list}</li>)}
        </ol>
        </li>
        </ul>
        </p>
      </div>

      <div className='typesDiv'>
      <h1 className='head2'>Uses of Blockchain Mining</h1>
      <ol>
        <li><b>Validating Transactions: </b>
        Bitcoin transactions take place in huge figures every day. Cryptocurrencies function without 
        a central administrator and the insecurity can be substantial with the transactions that 
        transpire. So, what is the authentication method with such cryptocurrencies? With each 
        transaction, new blocks are added to the blockchain in the network and the validation lies 
        in the mining results from the blockchain miners.
        </li><br/>
        <li><b>Confirming Transactions: </b>
        Miners work the blockchain mining process to confirm whether the transaction is authentic or 
        not. All confirmed transactions are then included in the blockchain.
        </li><br/>
        <li><b>Securing Network: </b>
        To secure the transaction network, bitcoin miners work together. With more users mining the 
        blockchain,  the blockchain network security increases. Network security ensures that there 
        are no fraudulent activities happening with cryptocurrencies.
        </li>
      </ol>
      </div>
    </>
    
  )
}

export default Mining