import React, { useEffect, useState } from "react";
import { FirstSection } from "./FirstSection";
// import FivthSection from "./FivthSection";
import FourthSection from "./FourthSection";
import SlideShow from "./SlideShow";
import ThirdSection from "./ThirdSection";
import axios from "axios";
import CryptoData from "./CryptoData";
import Marquee from "react-fast-marquee";
import Maintenance from "./Maintenance";

const Home = () => {
  const [coins, setCoins] = useState([]);

  useEffect(() => {
    axios
      .get(
        "https://api.coingecko.com/api/v3/coins/markets?vs_currency=inr&order=market_cap_desc&per_page=50&page=1&sparkline=false"
      )
      .then((res) => {
        setCoins(res.data);
      })
      .catch((error) =>
        alert("There might be something wrong with the API" + error)
      );
  }, []);
  return (
    <div>
      <Maintenance />
      {/* <SlideShow />
      <Marquee speed={50} gradient={false} pauseOnHover={true} className="marquee">
        {coins.map((item) => (
          <div className="cryptoData">
            <CryptoData
              key={item.id}
              src={item.image}
              name={item.name}
              symbol={item.symbol}
              price={item.current_price}
            />
          </div>
        ))}
      </Marquee> */}

      {/* <FirstSection /> */}
      {/* <ThirdSection /> */}
      {/* <FourthSection /> */}
    </div>
  );
};

export default Home;
